// components/my-business/dashboards/StaffDashboard.tsx
'use client';

import { useEffect, useState } from 'react';
import { db } from '@/src/firebaseConfig';
import { collection, getDocs, query, where, doc, getDoc } from 'firebase/firestore';
import { useUser } from '@/src/context/AuthContext';
import { Loader } from 'lucide-react';
import { Button } from '@/src/components/ui/button';
import { useRouter } from 'next/navigation';

interface StaffEntry {
  id: string;
  companyId: string;
  stationId?: string;
  role: string;
  companyName?: string;
  stationName?: string;
}

export default function StaffDashboard() {
  const { user } = useUser();
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [staffRoles, setStaffRoles] = useState<StaffEntry[]>([]);

  useEffect(() => {
    const fetchStaffRoles = async () => {
      if (!user?.uid) return;
      try {
        const snap = await getDocs(query(collection(db, 'staffs'), where('userId', '==', user.uid)));
        const results: StaffEntry[] = [];

        for (const docSnap of snap.docs) {
          const data = docSnap.data();
          const staffEntry: StaffEntry = {
            id: docSnap.id,
            companyId: data.companyId,
            stationId: data.stationId || '',
            role: data.role,
          };

          const companySnap = await getDoc(doc(db, 'rentalCompanies', data.companyId));
          if (companySnap.exists()) {
            staffEntry.companyName = companySnap.data().name;
          }

          if (data.stationId) {
            const stationSnap = await getDoc(doc(db, 'rentalStations', data.stationId));
            if (stationSnap.exists()) {
              staffEntry.stationName = stationSnap.data().name;
            }
          }

          results.push(staffEntry);
        }

        setStaffRoles(results);
      } catch (err) {
        console.error('‚ùå Error fetching staff roles:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchStaffRoles();
  }, [user]);

  if (loading) {
    return (
      <div className="flex justify-center items-center py-10">
        <Loader className="animate-spin w-6 h-6 text-gray-500" />
      </div>
    );
  }

  return (
    <div className="bg-white shadow rounded-xl p-4 sm:p-6 space-y-6">
      <h2 className="text-xl font-bold text-gray-800">Welcome, Staff üë∑</h2>

      <div className="space-y-6">
        {staffRoles.map((staff) => (
          <div
            key={staff.id}
            className="border p-4 sm:p-6 rounded-xl bg-gray-50 space-y-4 shadow-sm"
          >
            <div className="space-y-1">
              <h3 className="text-md font-semibold text-gray-700">Company</h3>
              <p className="text-gray-600">{staff.companyName || 'N/A'}</p>
            </div>
            <div className="space-y-1">
              <h3 className="text-md font-semibold text-gray-700">Station</h3>
              <p className="text-gray-600">{staff.stationName || 'N/A'}</p>
            </div>
            <div className="space-y-1">
              <h3 className="text-md font-semibold text-gray-700">Role</h3>
              <p className="text-gray-600 capitalize">{staff.role}</p>
            </div>

            <div className="pt-4 border-t">
              <h3 className="text-sm font-medium text-gray-700 mb-2">Quick Actions</h3>
              {staff.role === 'support' && (
                <div className="grid grid-cols-2 sm:flex flex-wrap gap-3">
                  <Button onClick={() => router.push('/rent')}>üö≤ Rent a Bike</Button>
                  <Button onClick={() => router.push('/return')}>üîÑ Return eBike</Button>
                </div>
              )}
              {staff.role === 'station_manager' && (
                <div className="grid grid-cols-2 sm:flex flex-wrap gap-3">
                  <Button onClick={() => router.push('/rent')}>üö≤ Rent a Bike</Button>
                  <Button onClick={() => router.push('/return')}>üîÑ Return eBike</Button>
                  <Button onClick={() => router.push('/stats')}>üìä Station Stats</Button>
                </div>
              )}
              {staff.role === 'technician' && (
                <div className="grid grid-cols-2 sm:flex flex-wrap gap-3">
                  <Button onClick={() => router.push('/maintain')}>üõ† Maintenance Logs</Button>
                </div>
              )}
              {staff.role === 'company_admin' && (
                <div className="grid grid-cols-2 sm:flex flex-wrap gap-3">
                  <Button onClick={() => router.push('/manage-staff')}>üë• Manage Staff</Button>
                  <Button onClick={() => router.push('/ebikes')}>üö≤ eBike Management</Button>
                  <Button onClick={() => router.push('/rental-orders')}>üì¶ Rental Orders</Button>
                </div>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}